.NavbarItems{
    background: transparent;
    height: 80px;
    display: flex;
    justify-content: right;
    align-items: center;
    font-size: 18px;
    padding: 10px;
    position: relative;
}

.nav-menu{
    display: grid;
    grid-template-columns: repeat(5, auto);
    grid-gap: 30px;
    list-style: none;
    text-align: left;
    width: 90vw;
    justify-content:  end;
    margin-right: auto;
    
}

.nav-links{
    color: hsl(130, 18%, 93%);
    text-decoration: none;
    padding: 0.5rem 1rem;
}

.nav-links:hover{
    background: #66fcf1;
    border-radius: 4px;
    transition: all 0.2s ease-out;
    color: black;
}

.nav-links-mobile{
    display: none;
}
.fa-bars{
    color: #66fcf1;

}

.menu-icon{
    display: none;
}

@media screen and (max-width:960px){
    .NavbarItems{
        position: relative;
    }
    .nav-menu{
        display: flex;
        flex-direction: column;
        width: 100%;
        height: 500px;
        position: absolute;
        top : 80px;
        left: -1000px;
        opacity: 1;
        transition: all 0.5s ease;
   }
   .nav-menu.active{
       background: #0a192f;
       left: 0;
       opacity: 1;
       transition: all 0.5s ease;
       z-index: 1;
   }
   .nav-links{
        text-align: center;
        padding: 2rem;
        width: 70%;
        display: table;
   }
   .nav-links:hover{
       background-color: #66fcf1;
       border-radius: 10px;
   }
   .menu-icon{
       display: block;
       position: absolute;
       top: 0;
       right: 0;
       transform: translate(-100%,60%);
       font-size: 1.8rem;
       cursor: pointer;
   }
   .fa-times{
        color:#66fcf1;
        font-size: 2rem;  
    }

    .nav-links-mobile{
        display: block;
        text-align: center;
        padding: 2rem;
        margin: 1rem -1.5;
        border-radius: 4px;
        width: 70%;
        background-color: #66fcf1;
        border-radius: 10px;
        outline: none;
        text-decoration: none;
        color: black;
        font-size: 1.5rem;
    }

    .nav-links-mobile:hover{
        background:white;
        color: #0a192f;
        transition: 250ms;
    }
    Button{
        display: none;
    }




}



