
.btn{
    background-color: transparent;
    border-radius: 25px;
    outline: none;
    border: solid;
    border-color: #66fcf1;
    cursor: pointer;

}

.btn:hover{
    background-color: #66fcf1;
    color:#0a192f;
    border-radius: 10px;
    transition: all 0.2s ease-out;

}

.btn--outline{
    background-color: transparent;
    color: #66fcf1;
    border-radius: 10px;
    transition: all 0.3s ease-out;

}

.btn--medium{
    padding: 8px 20px;
    border-radius: 10px;
    font-size: 18px;
    color: white;

}

.btn--large{
    border-radius: 10px;
    font-size: 18px;
    color:white;
}